/* src/components/css/StationGrid.css */

/* Container for the StationGrid */
.scorecard-container {
  margin: 20px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #f9f9f9;
  display: flex;
  flex-direction: column;
  gap: 10px; /* Space between station rows */
  overflow: hidden; /* Prevent overflow */
}

/* Space between station rows */
.station-row {
  display: flex;
  align-items: center; /* Align name and grid to the center vertically */
  justify-content: flex-start; /* Align horizontally to the left */
  flex-wrap: wrap; /* Allow the station grid to wrap */
}

/* Style for the station name */
.station-name {
  margin-right: 10px; /* Space between the name and grid */
  font-weight: bold;
  font-size: 16px;
  text-align: left; /* Left-align the station names */
  flex-shrink: 0; /* Prevent shrinking */
}

/* Grid container for each station's shots */
.station-grid {
  display: grid;
  gap: 5px;
  grid-template-columns: repeat(auto-fill, minmax(40px, 1fr)); /* Automatically fill the space with columns */
  grid-auto-rows: 40px; /* Keep rows consistent */
  justify-items: start; /* Align grid items (shots) to the left */
  width: 100%; /* Ensure grid takes up available width */
  max-width: 100%; /* Ensure the grid does not exceed the container's width */
  box-sizing: border-box; /* Include padding/border in the width calculation */
  overflow-x: auto; /* Allow horizontal scrolling only if necessary */
}

/* Shot styling */
.shot {
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 5px;
  cursor: default;
  display: flex;
  justify-content: flex-start; /* Align content inside the shot to the left */
  align-items: center; /* Ensure the text (e.g., TP) is centered vertically */
}

/* Add a color for hit shots */
.shot.hit {
  background-color: green;
  color: white;
}

/* Add a color for missed shots */
.shot.miss {
  background-color: red;
  color: white;
}

/* Empty shot state */
.shot.empty {
  background-color: lightgrey;
}

/* Responsive design: for medium screens */
@media (max-width: 675px) {
  .station-grid {
    grid-template-columns: repeat(6, 40px); /* 6 columns per row for smaller screens */
  }
}

/* Responsive design: for smaller screens */
@media (max-width: 575px) {
  .station-grid {
    grid-template-columns: repeat(4, 40px); /* 4 columns per row for even smaller screens */
  }
}
