body {
  font-size: 14px;
}

input, textarea, select {
  font-size: 16px; /* Set minimum font size to prevent zooming */
}

/* Tablet styles */
@media (min-width: 768px) {
  body {
    font-size: 16px;
  }
}

/* Desktop styles */
@media (min-width: 1024px) {
  body {
    font-size: 18px;
  }
}

:root {
  --max-width: 400px; /* Define the max-width value as a CSS variable */
}