.record-shots {
  text-align: center;
}

.station-input {
  margin-bottom: 10px;
  padding: 5px;
  font-size: 16px;
}

.button-container {
  margin: 20px 0;
  display: flex;
  justify-content: center;
  gap: 20px; /* Space between buttons */
}

.shot-button {
  padding: 20px;
  margin: 10px;
  border: none;
  border-radius: 10px;
  font-size: 24px;
  width: 100px;
  height: 100px;
  cursor: pointer;
}

.shot-button.hit {
  background-color: green;
  color: white;
}

.shot-button.miss {
  background-color: red;
  color: white;
}

.shot-button.undo {
  background-color: grey;
  color: white;
}

.station-grid {
  display: grid;
  gap: 5px;
}

.shot {
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 5px;
  cursor: default;
}

.shot.hit {
  background-color: green;
  color: white;
}

.shot.miss {
  background-color: red;
  color: white;
}

.shot.empty {
  background-color: lightgrey;
}

.record-button,
.return-button {
  background-color: blue;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
  width: 150px; /* Ensure both buttons are the same width */
}

.return-button {
  background-color: #007bff; /* Specific color for the return button */
}

.record-button:hover,
.return-button:hover {
  background-color: #0056b3; /* Darker shade for hover effect */
}

/* Style for True Pair shots */
.shot.true-pair {
  background-color: lightgrey; /* Background color for True Pair */
  color: #6d6d6d; /* Slightly darker grey for the font */
  font-weight: bold; /* Optional: Make the text bold */
}

.shot.true-pair:hover {
  background-color: grey; /* Darker color on hover */
  color: #4d4d4d; /* Even darker grey for hover state */
}

/* Centering the Current Station Preview */
.selected-station-preview {
  margin-top: 20px; /* Space above the preview */
  text-align: center;
}

.selected-station-preview .station-grid {
  display: grid;
  gap: 5px;
  justify-content: center; /* Center the grid horizontally */
  margin: 0 auto; /* Center the grid horizontally */
}

/* Increase the size of the station select dropdown */
.station-select {
  padding: 12px 15px; /* Increase padding for larger size */
  font-size: 18px; /* Increase font size */
  width: 200px; /* Increase the width */
  border-radius: 8px; /* Slightly rounded corners */
  border: 2px solid #007bff; /* Border color for clarity */
  background-color: #f0f0f0; /* Light background for visibility */
  cursor: pointer;
  text-align: center; /* Center the text inside the dropdown */
}

.station-select:focus {
  outline: none; /* Remove default outline */
  border-color: #0056b3; /* Border color when focused */
  box-shadow: 0 0 5px rgba(0, 86, 179, 0.5); /* Add a light glow effect */
}

/* Make the label bigger and more prominent */
label {
  font-size: 20px; /* Increase font size of the label */
  font-weight: bold; /* Make the label text bold */
  margin-bottom: 15px; /* Space between label and dropdown */
  display: block; /* Ensure the label is on its own line */
  color: #333; /* Darker color for better readability */
}
